import { Box, Heading, FormControl, FormLabel, Input, Button } from '@chakra-ui/react';
import { useContext, useState } from 'react';
import { useToast } from '@chakra-ui/react'
import { useNavigate } from 'react-router-dom';
import Context from '../Context/Context';
import './Login.css'

function Login() {
  const history=useNavigate();
  const {setuser}=useContext(Context)
  const [email,setemail]=useState("")
  const [password,setpassword]=useState("")
  const toast = useToast()


  // handle Guest
  const handleGuest=(e)=>{
    e.preventDefault();
    setemail("guest@gmail.com")
    setpassword("123456")
  }
  

  // handle submit
  const handleSubmit=async (e)=>{
    e.preventDefault();
    document.querySelector(".Overlay").style.zIndex = "1001";
    // console.log(email,password)
    const response = await fetch('https://mern-chat-app-steel.vercel.app/handleuser/Login/', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ Email:email, password:password})
    });
    const json = await response.json()
    document.querySelector(".Overlay").style.zIndex = "-1";
    // console.log(json)
    if (json.sucess === true) {
      toast({
        title: 'Account Logged in .',
        description: "Successfully Logged account.",
        status: 'success',
        duration: 4000,
        isClosable: true,
      })
      window.localStorage.setItem("Auth",json.Auth)
      window.localStorage.setItem("user",JSON.stringify(json.user))
      setuser( (json.user));
      setemail('')
      setpassword('')
      history("/Home")
    }
    else {
      if(json.messages==="Email not registered"){
        toast({
          title: 'Incorrect credentials',
          description: 'Email Not Registered',
          status: 'success',
          duration: 4000,
          isClosable: true,
        })
      }
      else{
        toast({
          title: 'Incorrect credentials',
          description: 'Incorrect credentials',
          status: 'success',
          duration: 4000,
          isClosable: true,
        })
      }
    }
  }

  return (
    <Box
      display="flex"
      justifyContent="center"
      alignItems="center"
      margin={'auto'}
          bgGradient="linear(to right, #667eea, #764ba2)"
    >
      <Box
        p={8}
        maxW="400px"
        bg="white"
        rounded="lg"
        boxShadow="lg"
      >
        <Heading mb={6} textAlign="center" color="gray.700">Login</Heading>
        <FormControl isRequired id="email" mb={4}>
          <FormLabel color="gray.700">Email address</FormLabel>
          <Input color="gray.700" type="email" placeholder="Enter your email" value={email} required={true} onChange={(e)=>{setemail(e.target.value)}} />
        </FormControl>
        <FormControl isRequired id="password" mb={6}>
          <FormLabel color="gray.700">Password</FormLabel>
          <Input color="gray.700" type="password" placeholder="Enter your password" value={password} required={true} onChange={(e)=>{setpassword(e.target.value)}} />
        </FormControl>
        <Button colorScheme="purple" size="lg" width="full" onClick={handleSubmit}>
          Sign In
        </Button>
        <Button colorScheme="yellow" size="lg" width="full" mt={'4px'} onClick={handleGuest}>
          Get Guest Credentials
        </Button>
      </Box>
      
    <div className="Overlay" ></div>
    </Box>
  );
}

export default Login;
