import React, { useContext, useEffect, useState } from 'react'
// import Context from '../Context/Context.js';
import Context from '../Context/Context';
import { Box,Text,Button } from '@chakra-ui/react';
import UserChat from './UserChat';
import LazySkeleton from '../Miscellenous/LazySkeleton';
import ChatModal from '../GroupChat/ChatModal';

const Mychats = ({refresh}) => {
  const [loading,setloading]=useState(false)
  const {allchats,setallchats,user}=useContext(Context)
  // console.log("user is ",window.localStorage.getItem('user'))
  
  const getchats=async ()=>{
    setloading(true)
    const response=await fetch('https://mern-chat-app-steel.vercel.app/Chat/acesschats/', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        "Authorization":window.localStorage.getItem("Auth")
      },
    });
    const data=await response.json()
    setallchats(data)
    setloading(false)
    
    
  }
  useEffect(()=>{
    getchats();
    
  },[refresh])
  // {console.log(allchats)}
  return (
    <div style={{'color':'black'}} >
      <Box display={'flex'} justifyContent={'space-between'}padding={'10px'}>
        <Box> <Text fontSize={{ base: "1rem", sm: "2rem", md: "1.5rem" }}>MY CHATS </Text></Box>
        <Box  height={'fit-content'}  ><ChatModal/>   </Box>
      </Box>
      <Box  height={'2.5rem'} alignItems={'center'} display={'flex'} margin={'auto'} justifyContent={'center'} textAlign={'left'} borderRadius={'5px'}width={'fit-content'} padding={'20px'} backgroundColor={'#f7ef81'}>{user.Name}</Box>
      <Box  overflow-y={'auto'} backgroundColor={'#d4d4cb'} margin={'10px'} padding={'5px'} borderRadius={'10px'}>
        {loading && <LazySkeleton/>}

      {allchats && allchats.map((chat)=>{ return <UserChat key={chat._id}  currchat={chat} chatid={chat._id} isGroupChat={chat.isGroupChat} chatName={chat.chatName} users={chat.users} latestMessage={chat.latestMessage}/>})}

      
      </Box>
    </div>
  )
}

export default Mychats
