import React from "react";
import { Tabs, TabList, TabPanels, Tab, TabPanel } from "@chakra-ui/react";
import { Container, VStack } from '@chakra-ui/react'
import Login from "./AuthUser/Login";
import Signin from "./AuthUser/Signin";
import './HandleUser.css'
// import img from '../Images/Back.jpg'
const HandleUser = () => {
    return (
        <div >
            <VStack
                display={"flex"}
                justifyContent={'center'}
                height={'100vh'}
                backgroundImage={`url('../Images/Back.jpg')`}
            >               
                <section style={{"marginBottom":"9vh"}}>
                    <div className="content">
                        <h2>ChatZen</h2>
                        <h2>ChatZen</h2>
                    </div>
                </section>
                    
            
                <Container justifyContent={'center'} maxW='20rem'  borderRadius={'10px'} bg='blue.600' color='white' display={'flex'}   >



                    <Tabs variant='unstyled' >
                        <TabList

                        >
                            <Tab borderRadius={'10px'} ml={'15px'} _selected={{ color: 'white', bg: 'blue.500' }}>Login</Tab>
                            <Tab borderRadius={'10px'} _selected={{ color: 'white', bg: 'green.400' }}>Sign Up</Tab>
                        </TabList>
                        <TabPanels>
                            <TabPanel>
                                <Login />
                            </TabPanel>
                            <TabPanel>
                                <Signin />
                            </TabPanel>
                        </TabPanels>
                    </Tabs>
                </Container>
            </VStack>
        </div>
    );
};

export default HandleUser;
