import React, { useContext } from 'react'
import { Box, Text, Button, Container, Input, InputGroup, InputRightElement } from '@chakra-ui/react';
import Context from '../Context/Context'
import { CheckIcon } from '@chakra-ui/icons';

const DisplayMessage = ({message,readBy}) => {
    const {user}=useContext(Context)
  return (
    <div style={{"display":'flex',"alignContent":"center" ,"justifyContent":`${message.sender._id===user._id?"right":"left"}`}} >
        {user && message.sender._id===user._id  ? <Box maxW={'30vw'} width={'fit-content'} padding={'5px'}  borderRadius={'5px'} color={'white'} margin={'5px'}  backgroundColor={'#69c151'}>{message.content}</Box>:message.chat.isGroupChat ?<Box display={'flex'} flexDirection={'column'}  width={'fit-content'} padding={'5px'} maxW={'30vw'} borderRadius={'5px'} color={'white'} margin={'5px'}  backgroundColor={'#a78ff5'}>
           <Box display={'flex'} justifyContent={'left'}><Text fontSize={'0.7rem'}>~{message.sender.Name}</Text></Box> <Box><Box >{message.content}</Box></Box>
        </Box>:<Box maxW={'30vw'} width={'fit-content'} padding={'5px'}  borderRadius={'5px'} color={'white'} margin={'5px'}  backgroundColor={'#a78ff5'}>{message.content}</Box>}
        {/* {message.readBy.length===message.chat.users.length?console.log("readed by all user"):console.log("not readed by all user")} */}
        {message.sender._id===user._id && <CheckIcon w={'10px'} mt={'8px'} color={(message.readBy.length===message.chat.users.length) || readBy.length===message.chat.users.length ?"#07bdf9":"black"}></CheckIcon>}
      
    </div>
  )
}

export default DisplayMessage
