import React from 'react'
import { Card, CardHeader, CardBody, CardFooter, Container, Box } from '@chakra-ui/react'
import { Image,Button,Heading,Text, Stack,Avatar } from '@chakra-ui/react'
const UserList = (props) => {
    return (
        <div style={{"marginBottom":"8px"} } onClick={props.click}>
           <Container border={'2px solid #d0cece'}  _hover={{ bg: "yellow" }}  borderRadius={'5px'} display={'flex'} justifyContent={'space-evenly'} m={'0px'} p={'4px'}>
                <Avatar size='sm' name='Segun Adebayo' src={`https://robohash.org/${props.Name}.png`} />
                <Box ml={'2px'} width={'80%'}>
                    <Text>{props.Name}</Text>
                    <span>
                        <Text fontSize={'12px'}><b>Email:</b>{props.email}</Text>
                    </span>
                </Box>


           </Container>



        </div>
    )
}

export default UserList
