import { Box } from '@chakra-ui/react'
import React, { useContext, useState } from 'react'
import SideDrawer from './SideDrawer/SideDrawer'
import Mychats from './Chats/Mychats'
import Chats from './Chats/Chats.js'
import { useMediaQuery } from "@chakra-ui/react";
import Context from './Context/Context'


const Home = () => {
  const [isSmallerThan450] = useMediaQuery("(max-width: 750px)");
  const {selectedchat}=useContext(Context)
  const [refresh,setrefresh]=useState(false)
  // console.log("selelctedcha",selectedchat)

  return (
    <div style={{"height":"100vh"}}> 
    <div style={{"width":"10vw"}}><SideDrawer/></div>
    <Box display={'flex'} color={'white'} gap={'10px'} padding={'10px'}>
      <Box width={`${isSmallerThan450?'100%':'40%'}`} display={isSmallerThan450?selectedchat?'none':'block':'block'} >

      <div style={{"border":"2px solid green","overflowY":'auto','borderRadius':"10px",'height':"85vh","backgroundColor":"white",'padding':'10px'}} ><Mychats refresh={refresh}/></div>
      </Box>
      <Box width={`${isSmallerThan450?'100%':'60%'}`} display={`${isSmallerThan450?selectedchat?'block':'none':'block'}`}>
      <div style={{"border":"2px solid green",'borderRadius':"10px",'height':"85vh","backgroundColor":"white"}} ><Chats refresh={refresh} setrefresh={setrefresh}/></div>
    </Box>
      </Box>


      
    </div>
  )
}

export default Home
