import React, { useState,useContext } from 'react'
import { ViewIcon } from '@chakra-ui/icons'
import { Box, Button, Text, Input, Stack, useToast,Spinner } from '@chakra-ui/react';
import Context from '../Context/Context';
import UserBadge from './UserBadge';
import UserList from '../SideDrawer/UserList';
import {
    Modal,
    ModalOverlay,
    ModalContent,
    ModalHeader,
    ModalFooter,
    ModalBody,
    ModalCloseButton,
} from '@chakra-ui/react'
  import { useDisclosure } from '@chakra-ui/hooks';
const GroupChatModal = ({refresh,setrefresh}) => {
    const toast = useToast()
    const {selectedchat,setselectedchat,user}=useContext(Context)
    const [groupname,setgroupname]=useState("")
    const [search,setsearch]=useState('')
    const [searchresults,setsearchresults]=useState([])
    const [loading,setloading]=useState(false)
    const [delloading,setdelloading]=useState(false)
    const [Selecteduser, setSelecteduser] = useState([]);
    const { isOpen, onOpen, onClose } = useDisclosure()

    // handle remove
    const handleremove=(usr)=>{
        setSelecteduser(Selecteduser.filter((sel) => sel._id !== usr._id));
    }
    // handle handlepermanenetremove
    const handlepermanenetremove=async (usr)=>{
        if(selectedchat.groupAdmin._id!==user._id){
            toast({
                title: '',
                description: "Only Group admin can remove users",
                status: 'success',
                duration: 9000,
                isClosable: true,
            })
            return;
        }
        if(selectedchat.users.length===3){
            toast({
                title: '',
                description: "Group should have at least 3 users",
                status: 'success',
                duration: 9000,
                isClosable: true,
            })
            return;

        }
        try{

        
        setdelloading(true)
        const response = await fetch(`https://mern-chat-app-steel.vercel.app/Chat/remove`, {
                    method: 'DELETE',
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': window.localStorage.getItem('Auth')
                    },
                    body: JSON.stringify({chatId:selectedchat._id,userId:usr._id})
                });
                const json = await response.json()
                setdelloading(false)
                setselectedchat(json)
                setrefresh(!refresh)
                toast({
                    title: '',
                    description: "User Removed Successfully",
                    status: 'success',
                    duration: 9000,
                    isClosable: true,
                })
            }
            catch{
                toast({
                    title: '',
                    description: "Cann't reach to servers",
                    status: 'success',
                    duration: 9000,
                    isClosable: true,
                })
            }

    }
    const Adduser = (user) => {
        if (Selecteduser.includes(user)  || selectedchat.users.some(obj => obj._id === user._id)) {
            toast({
                title: '',
                description: "User Already Added",
                status: 'success',
                duration: 9000,
                isClosable: true,
            })
            return;
        }
        setSelecteduser([...Selecteduser, user])
    }
    // handlesubmit
    const handlesubmit=async()=>{
        if((groupname===selectedchat.chatName || groupname==='') && Selecteduser.length===0){
            setgroupname("")
            setSelecteduser([])
            onClose()
            return ;
        }
        try{        
        if(groupname!==selectedchat.chatName && groupname!==''){
            const response = await fetch(`https://mern-chat-app-steel.vercel.app/Chat/renamegroup/`, {
                    method: 'PUT',
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': window.localStorage.getItem('Auth')
                    },
                    body: JSON.stringify({chatId:selectedchat._id,chatName:groupname})
                });
                const json = await response.json()
                setselectedchat(json)
        }
        if(Selecteduser.lenght!==0){
            let alluserid=[]
            Selecteduser.map((usr)=>{alluserid.push(usr._id)})         
            const respose=await fetch(`https://mern-chat-app-steel.vercel.app/Chat/addtogroup/`, {
                method: 'PUT',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': window.localStorage.getItem('Auth')
                },
                body: JSON.stringify({chatId:selectedchat._id,userId:alluserid})
            })  
            const data=await respose.json();
            setselectedchat(data);
            toast({
                title: '',
                description: "Group Successfully updated",
                status: 'success',
                duration: 9000,
                isClosable: true,
            })
            
        }
        setgroupname("")
        setSelecteduser([])
        onClose()
        setrefresh(!refresh)

        }
        catch{
            toast({
                title: '',
                description: "Couldn't Update Group",
                status: 'success',
                duration: 9000,
                isClosable: true,
            })
            setgroupname("")
            setSelecteduser([])
            onClose();
        }
    }
    // handlesearch
    const HandleSearch = async(e) => {
        setsearch(e.target.value)
        // console.log(search)
        if (e.target.value !== '') {
            try {
                setloading(true)
                const response = await fetch(`https://mern-chat-app-steel.vercel.app/handleuser/Alluser?search=${e.target.value}`, {
                    method: 'GET',
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': window.localStorage.getItem('Auth')
                    },
                    body: JSON.stringify()
                });
                const json = await response.json()
                setloading(false)
                setsearchresults(json)
            }
            catch{
                toast({
                    title: 'Error',
                    description: "Couldn't fetch user",
                    status: 'success',
                    duration: 9000,
                    isClosable: true,
                })

            }
        }
    }
    // handle exit 
    const handlexit=async ()=>{
        try{

            const response = await fetch(`https://mern-chat-app-steel.vercel.app/Chat/remove`, {
                        method: 'DELETE',
                        headers: {
                            'Content-Type': 'application/json',
                            'Authorization': window.localStorage.getItem('Auth')
                        },
                        body: JSON.stringify({chatId:selectedchat._id,userId:user._id})
                    });
                    const json = await response.json()
                    setselectedchat(json)
                    setrefresh(!refresh)
                    onClose();
                    toast({
                        title: 'Success',
                        description: "Successfully exited from group",
                        status: 'success',
                        duration: 9000,
                        isClosable: true,
                    })
        }
        catch{
            toast({
                title: 'Error',
                description: "Couldn't redirected to  server",
                status: 'success',
                duration: 9000,
                isClosable: true,
            })
        }

    }


  return (
    <div>
        <div style={{ "color": "black" }} onClick={()=>{onOpen()}}>{<ViewIcon boxSize={6} w={8} h={8} mr={'5px'} color="black"/>}</div>
        <Modal isOpen={isOpen} onClose={onClose}>
                <ModalOverlay />
                <ModalContent>
                    <ModalHeader>Update Group Chat</ModalHeader>
                    <ModalCloseButton />
                    <ModalBody>
                        {!delloading && <Box display={'flex'} overflowY={'auto'}>{selectedchat.users.map((usr)=>{return <UserBadge groupchatmodel={true} color={'purple'} key={usr._id} user={usr} click={()=>{handlepermanenetremove(usr)}}/>})}</Box>}
                        <Stack spacing={3}>
                            <Input variant='filled' placeholder={selectedchat.chatName } value={groupname} onChange={(e) => { setgroupname(e.target.value) }} />
                            <Input variant='outline' placeholder='Add people' value={search} onChange={(e) => { HandleSearch(e) }} />
                        </Stack>
                        <Box
                        display={'flex'}
                        overflowY={'auto'}
                        >
                            {Selecteduser.map((usr)=>{return <UserBadge  color={'blue'} key={usr._id} user={usr} click={()=>{handleremove(usr)}}/>})}
                        </Box>
                        <Box
                        maxH={'40vh'}
                        overflow={'auto'}
                        >
                            {loading && <Spinner />}
                            {search!=='' && searchresults.map((usr) => {
                                return <UserList key={usr._id} click={() => { Adduser(usr) }} Name={usr.Name} email={usr.email} />
                                
                            })}
                        </Box>



                    </ModalBody>

                    <ModalFooter display={'flex'}justifyContent={'space-between'}>

                        <Button backgroundColor={'red'} onClick={handlexit} variant='ghost'>Exit Group</Button>
                        <Button backgroundColor={'blue'} onClick={handlesubmit} variant='ghost'>Update Group</Button>
                    </ModalFooter>
                </ModalContent>
            </Modal>

      
    </div>
  )
}

export default GroupChatModal
