import React, { useContext } from 'react'
import { Box, Button, Text, Input, Stack, useToast,Spinner } from '@chakra-ui/react';
import {CloseIcon} from '@chakra-ui/icons'
import Context from '../Context/Context';

const UserBadge = (props) => {
  const {user,selectedchat} =useContext(Context)
  return (
    
        <Box display={'flex'} alignItems={'center'} gap={'2px'} m={'5px'} backgroundColor={props.user._id!==user._id?props.color:"green"} borderRadius={'5px'} paddingInline={'5px'} color={'white'} >
            <Text color={'white'}>
                {user._id===props.user._id ?"You": props.user.Name}
            </Text>
            {props.groupchatmodel?user._id===selectedchat.groupAdmin._id && props.user._id!==user._id&&  <CloseIcon w={2} h={2} onClick={props.click}/>:<CloseIcon w={2} h={2} onClick={props.click}/>}
        </Box>      
    
  )
}

export default UserBadge
