import React, { useContext, useEffect, useState,useRef } from 'react'
import Context from '../Context/Context'
import { Box, Text, Button, Container, Input, InputGroup, InputRightElement,Spinner } from '@chakra-ui/react';
import DisplayMessage from './DisplayMessage';
import io from "socket.io-client"
import gif from '../gif/typing.gif'
 
const ENDPOINT="https://chat-app-nine-sepia-15.vercel.app/";
var socket,selectedChatCompare;
const Usermessages = ({refresh,setrefresh}) => {
  const containerRef = useRef(null);
    const {selectedchat,notification,setnotification,type,istype,typingUsers,setTypingUsers}=useContext(Context)
    const [allmessage,setallmessage]=useState([])
    const [mess, setmess] = useState('')
    const [loading,setloading]=useState(false)
    const [socketConnected,setSocketConnected]=useState(false)
    const [typing,setTyping]= useState(false)
    const [isTyping,setIsTyping]=useState(false)
    const user=JSON.parse(window.localStorage.getItem("user"));
    const [readed,setreaded]=useState([]);
    
     useEffect(()=>{
      socket=io(ENDPOINT);
      // console.log("user is",JSON.parse(window.localStorage.getItem("user")))
      user && socket.emit("setup",user);
      socket.on('connected',()=>{setSocketConnected(true)})
      

    },[])

    //  fetch all chats
    const fetchchats=async()=>{
        if(!user || !selectedchat){
            return ;
        }
        try{
          setallmessage('')
            setloading(true)
            const response = await fetch(`https://mern-chat-app-steel.vercel.app/messages/${selectedchat._id}`, {
            method: 'Get',
            headers: {
                'Content-Type': 'application/json',
                "Authorization": window.localStorage.getItem("Auth") 
            }
            })
            const data= await response.json();
            // console.log("data is ",data)
            setloading(false)
            setallmessage(data)
            socket.emit("join chat",selectedchat._id,user)
            }
            catch{
                return
            }
    }
    // handle typing
    const handletyping=(e)=>{
      setmess(e.target.value)
      // console.log("ugala")
      if(!socketConnected) return ;
      if(!typing){
        setTyping(true)
        socket.emit("typing",selectedchat._id)
        socket.emit("Typing",selectedchat,user)
      }
      let lastTypingTime=new Date().getTime();
      var timerLength=3000;
      setTimeout(() => {
        var timenow=new Date().getTime();
        var timediff=timenow-lastTypingTime;
        if(timediff>=timerLength && typing){
          socket.emit("stop typing",selectedchat._id)
          socket.emit("Stop Typing",selectedchat,user)
          setTyping(false);
          // istype(false)
        }        
      }, timerLength);
    }
    // 
    // send messages
    const handleClick = async (e) => {
        if(e && e.key !== "Enter"){
          return ;
    
        }
        const temp=mess;
        setmess('');
        socket.emit("stop typing",selectedchat._id)
        const response = await fetch('https://mern-chat-app-steel.vercel.app/messages/send/', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            "Authorization": window.localStorage.getItem("Auth")
          },
          body: JSON.stringify({ content:temp, chatId: selectedchat._id })
        })
        const data = await response.json();
        socket.emit("new message",data)
        data && data.readBy && data.readBy.map((usr)=>{if(!readed.includes(usr)){setreaded( [...readed, usr]);}})
        // console.log("readed by users",data.readBy)
        setallmessage([...allmessage, data])
        setrefresh(!refresh)
    
      }


      // for message recieved
      useEffect(()=>{
        socket.on("increase",(userId)=>{
          // console.log("User arrived",userId)
          if(!readed.includes(userId)){
            setreaded( [...readed, userId]);
            // console.log("new user readed ",readed)
          }
        })
        
      socket.on('typing',()=>(setIsTyping(true),istype(true)))
      socket.on("Typing",(userId)=>{
        setTypingUsers(prevTypingUsers => {
          const updatedTypingUsers = [...prevTypingUsers];
  
          // Check if the user is already in the typing users list
          const userIndex = updatedTypingUsers.findIndex(user => user === userId);
  
          if (userIndex !== -1) {
            // ///////////////////////do nothing/////////////////
          } else {
            // User is new, add to the typing users list
            updatedTypingUsers.push( userId );
          }
  
          return updatedTypingUsers;
        });
      })
      socket.on('Stop Typing',(userId)=>(
        setTypingUsers(prevTypingUsers => {
          const updatedTypingUsers = prevTypingUsers.filter(user => user !== userId);
          return updatedTypingUsers;
        })
      ))
      socket.on('stop typing',()=>(setIsTyping(false),istype(false)))
        socket.on("message recieved",(newMessageRecieved)=>{
          if(!selectedChatCompare || selectedChatCompare._id!==newMessageRecieved.chat._id){
            if(!notification.includes(newMessageRecieved)){
              setnotification([newMessageRecieved, ...notification]);
              setrefresh(!refresh);
            }
          }
          else{
            setallmessage([...allmessage,newMessageRecieved])
          }
        })
      })



     
     

    useEffect(()=>{
        fetchchats();   
        selectedChatCompare=selectedchat;     
      },[selectedchat])
      // Set the scroll position to the bottom of the container



    useEffect(() => {
      const container = containerRef.current;

      if (container) {
        container.scrollTo({
          top: container.scrollHeight,
          behavior: 'smooth'
        });
      }
  }, [allmessage]);

  return (
    <Box>
    
    
        <Box height={'70vh'} ref={containerRef} overflow={'auto'}  backgroundColor={'#e2e2e0'} margin={'2px'} borderRadius={'10px'} padding={'0px'}>

        {loading && <Spinner size='xl' marginTop={'25%'} />}
          {!selectedchat ? <Box><Text fontSize={'5rem'} display={'flex'} flexDirection={'column'}><span> WelCome</span> <span>To</span> <span>ChatZen</span>   </Text> </Box> : 
          allmessage.length>0 && allmessage.map((messe)=>{return <DisplayMessage  readBy={readed}  key={messe._id} message={messe}/>})
          // "oi"
          
        }
        
        {isTyping && <Box mb={'10px'} position={'fixed'} bottom={'12vh'} ml={'3px'}><img style={{'width':"30px","backgroundColor":"black"}} src={gif} alt="#" /></Box>}
      </Box>
      <Box display={'flex'}>
        {selectedchat ? <Box width={'100%'} mt={'5px'}>
          <InputGroup size='md' onKeyDown={(e)=>{handleClick(e)}}>
            <Input
              pr='4.5rem'
              type={'text'}
              placeholder='Text here'
              value={mess}
              onChange={(e) => {return  handletyping(e) }}
            />
            <InputRightElement width='4.5rem' color={'white'}>
              <Button h='1.75rem' size='sm' onClick={(e)=>{handleClick()}}><i className="fa-solid fa-paper-plane  fa-2xl"></i>
              </Button>
            </InputRightElement>

          </InputGroup>
        </Box>
          : ""}

      </Box>
      </Box>

  )
}

export default Usermessages
