import React, { useContext, useState } from 'react'
import { Box, Heading, FormControl, FormLabel, Input, Button, Tooltip, Toast,Text } from '@chakra-ui/react';
import {
    Drawer,
    DrawerBody,
    DrawerFooter,
    DrawerHeader,
    DrawerOverlay,
    DrawerContent,
    DrawerCloseButton,
  } from '@chakra-ui/react'
  import { useDisclosure } from '@chakra-ui/hooks';
import Title from './Title';
import User from './User';
import FindUser from './FindUser';
import Context from '../Context/Context';

const SideDrawer = () => {
    const { isOpen, onOpen, onClose } = useDisclosure()
  const btnRef = React.useRef()
  const [search,setsearch]=useState('');
  
  return (
    <div>
      <Box 
        width={'100vw'} 
        display={'flex'}
        justifyContent={'space-between'}
        alignItems={'center'}
        padding={'10px'}
      >
        <Button ref={btnRef} colorScheme='teal' onClick={onOpen}>
        <i className="fa-solid fa-magnifying-glass"></i>
        <Text display={{base:"none",md:"flex"}} color={'white'} padding={"5px"}> Search Users</Text>
      </Button>
      <Drawer
        isOpen={isOpen}
        placement='left'
        onClose={onClose}
        finalFocusRef={btnRef}
      >
        <DrawerOverlay />
        <DrawerContent>
          <DrawerCloseButton />
          <DrawerHeader>Find Users</DrawerHeader>

          <DrawerBody>
            <Input placeholder='search here...' value={search} onChange={(e)=>{setsearch(e.target.value)}}/>
            <Box mt={'10px'}>
            <FindUser onselect={onClose} search={search}/>
            </Box>
          </DrawerBody>

          
        </DrawerContent>
      </Drawer>



      <Box ><Title/></Box>
      <Box display={'flex'}
        justifyContent={'center'}
        alignItems={'center'}
        gap={'1vw'}
        
      >
      {/* <Box >
        <Tooltip label="Hey, I'm here!" aria-label='A tooltip'>

        <i className="fa-regular fa-bell fa-2xl" style={{"color": "#e70d2e"}}></i>
        </Tooltip>
      </Box> */}
      <Box><User/></Box>

      </Box>
      </Box>
      
    </div>
  )
}

export default SideDrawer
