import React, { useContext, useEffect, useState } from 'react'
import { useDisclosure } from '@chakra-ui/hooks';
import { Box, Button, Text, Input, Stack, useToast,Spinner } from '@chakra-ui/react';
import UserList from '../SideDrawer/UserList';
import {
    Modal,
    ModalOverlay,
    ModalContent,
    ModalHeader,
    ModalFooter,
    ModalBody,
    ModalCloseButton,
} from '@chakra-ui/react'
import Context from '../Context/Context';
import UserBadge from './UserBadge';


const ChatModal = () => {
    const toast = useToast()
    const [groupname, setgroupname] = useState('');
    const [search, setsearch] = useState('');
    const [Selecteduser, setSelecteduser] = useState([]);
    const [searchresults,setsearchresults]=useState([])
    const [loading,setloading]=useState(false)
    const { isOpen, onOpen, onClose } = useDisclosure()

    const {setallchats,allchats,setselectedchat}=useContext(Context)


    const HandleSearch = async(e) => {
        setsearch(e.target.value)
        // console.log(search)A
        if (e.target.value !== '') {
            try {
                setloading(true)
                const response = await fetch(`https://mern-chat-app-steel.vercel.app/handleuser/Alluser?search=${e.target.value}`, {
                    method: 'GET',
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': window.localStorage.getItem('Auth')
                    },
                    body: JSON.stringify()
                });
                const json = await response.json()
                setloading(false)
                setsearchresults(json)
            }
            catch{
                toast({
                    title: 'Error',
                    description: "Couldn't fetch user",
                    status: 'success',
                    duration: 9000,
                    isClosable: true,
                })

            }
        }
    }
    const handleremove=(usr)=>{
        setSelecteduser(Selecteduser.filter((sel) => sel._id !== usr._id));

    }
    const handlesubmit=async()=>{
        if(groupname===''){
            toast({
                title: 'Error',
                description: "Select group name",
                status: 'success',
                duration: 9000,
                isClosable: true,
            })
            return ;
        }
        if(Selecteduser.length<2){
            toast({
                title: 'Error',
                description: "Group Should have 2 or more users",
                status: 'success',
                duration: 9000,
                isClosable: true,
            })
            return ;
        }
        try{
            let alluserid=[]
            Selecteduser.map((usr)=>{alluserid.push(usr._id)})         
            const respose=await fetch(`https://mern-chat-app-steel.vercel.app/Chat/creategroupchat/`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': window.localStorage.getItem('Auth')
                },
                body: JSON.stringify({name:groupname,users:alluserid})
            })  
            const data=await respose.json();
            setallchats([data, ...allchats]);
            setselectedchat(data);
            // console.log("chat created",data)

            toast({
                title: 'Success',
                description: "Group Created Successfully",
                status: 'success',
                duration: 9000,
                isClosable: true,
            })
            onClose();
            return ;


        }
        catch(err){
            
            
            // console.log("chat couldnot created",err)
            toast({
                title: 'Error',
                description: "Failed in fetching",
                status: 'success',
                duration: 9000,
                isClosable: true,
            })
            return ;
        }

    }

    const Adduser = (user) => {
        if (Selecteduser.includes(user)) {
            toast({
                title: '',
                description: "User Already Added",
                status: 'success',
                duration: 9000,
                isClosable: true,
            })
            return;
        }
        setSelecteduser([...Selecteduser, user])
    }

    

    return (
        <div >
            <Button onClick={onOpen}>Create New Group <span  ><i className="fa-solid fa-plus"></i></span></Button>

            <Modal isOpen={isOpen} onClose={onClose}>
                <ModalOverlay />
                <ModalContent>
                    <ModalHeader>Create Group Chat</ModalHeader>
                    <ModalCloseButton />
                    <ModalBody>
                        <Stack spacing={3}>
                            <Input variant='filled' placeholder='ChatName' value={groupname} onChange={(e) => { setgroupname(e.target.value) }} />
                            <Input variant='outline' placeholder='Add people' value={search} onChange={(e) => { HandleSearch(e) }} />
                        </Stack>
                        <Box
                        display={'flex'}
                        overflowY={'auto'}
                        

                        >
                            {Selecteduser.map((usr)=>{return <UserBadge key={usr._id} color={'blue'} user={usr} click={()=>{handleremove(usr)}}/>})}
                        </Box>
                        <Box
                        maxH={'40vh'}
                        overflow={'auto'}
                        >
                            {loading && <Spinner />}
                            {search!=='' && searchresults.map((usr) => {
                                return <UserList key={usr._id} click={() => { Adduser(usr) }} Name={usr.Name} email={usr.email} />
                                
                            })}
                        </Box>



                    </ModalBody>

                    <ModalFooter>

                        <Button backgroundColor={'blue'} onClick={handlesubmit} variant='ghost'>Create Group</Button>
                    </ModalFooter>
                </ModalContent>
            </Modal>

        </div>
    )
}

export default ChatModal
