import React, { useContext, useEffect, useState } from 'react'
import { Skeleton, SkeletonCircle, SkeletonText, Stack } from '@chakra-ui/react'
import Context from '../Context/Context.js'
import UserList from './UserList.js';
import LazySkeleton from '../Miscellenous/LazySkeleton.js';
// import { Container, VStack } from '@chakra-ui/react'

const FindUser = (props) => {

    const data = useContext(Context);
    const [loading,setloading]=useState(false)
    const fetchalluser = async () => {
        // setloading(true)

        const response = await fetch('https://mern-chat-app-steel.vercel.app/handleuser/Alluser?search=', {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                'Authorization':window.localStorage.getItem('Auth')
            },
            body: JSON.stringify()
        });
        const json = await response.json()
        data.setAlluser(json)
        
        setloading(false)
    }


    //  create chat 
    const createchat=async(userId)=>{
        setloading(true)
        const response =await fetch('https://mern-chat-app-steel.vercel.app/Chat/',{
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization':window.localStorage.getItem('Auth')
            },
            body: JSON.stringify({userId})
        })
        const Data=await response.json();
        data.setselectedchat(Data)
        if (!data.allchats.find((c) => c._id === Data._id)) data.setallchats([Data, ...data.allchats]);
        props.onselect()
        // setloading(false)
    }
    useEffect(()=>{
        if (data.Alluser.length === 0) {
            setloading(true);
            fetchalluser();
        }

    },[])
    return (
        <div>
            {loading && 
            <LazySkeleton/>
            }
            {!loading && (data.Alluser.length !== 0) &&(props.search==='')?data.Alluser.map((usr)=>{return  <UserList key={usr._id} click={()=>{createchat(usr._id)}} Name={usr.Name} email={usr.email}/>}): data.Alluser.map((usr)=>{
                const name=usr.Name.toLowerCase();
                const mail=usr.email.toLowerCase();
                const srch=props.search.toLowerCase();
                if(name.startsWith(srch) || mail.startsWith(srch) ){
                    return   <UserList key={usr._id} click={()=>{createchat(usr._id)}} pic={usr.pic} Name={usr.Name} email={usr.email}/>
                }
                })}

        </div>
    )
}

export default FindUser
