import React, { useContext } from 'react'
import { Card, CardHeader, CardBody, CardFooter, Container, Box } from '@chakra-ui/react'
import { Image,Button,Heading,Text, Stack,Avatar } from '@chakra-ui/react'
import Context from '../Context/Context'
import {getsendername} from '../Logic/Sender.js'

const UserChat = (props) => {
    const {user,selectedchat,setselectedchat,typingUsers,setTypingUsers}=useContext(Context)  
    const getlatestsender=(latestMessage)=>{
        if(latestMessage.sender._id===user._id){
            return 'You';
        }
        return latestMessage.sender.Name;
    }
    const handlesubmit=()=>{
        return setselectedchat(props.currchat)
    }


  return (
    <div onClick={handlesubmit}>
      {/* {type && console.log("user id typing")} */}
         <div style={{"marginBottom":"2px" ,"margin":'8px',"borderRadius":"10px","backgroundColor":`${ selectedchat && (selectedchat._id==props.chatid)?'green':'white'}`,"color":`${ selectedchat && (selectedchat._id==props.chatid)?'white':'black'}`}}  >


           <Container  height={'fit-content'}    _hover={{ paddingLeft:"10px" ,paddingRight:"10px"}}  borderRadius={'5px'} display={'flex'} justifyContent={'space-evenly'} m={'0px'} p={'4px'} >
                <Avatar size='sm' name='Segun Adebayo' src={`https://robohash.org/${getsendername(props.users,user).Name}.png`} />
                <Box ml={'2px'} width={'80%'} >
                    <Text textAlign={'left'} color={`${ selectedchat && (selectedchat._id==props.chatid)?'white':'black'}`}>{(props.isGroupChat)?props.chatName:props.users && user&& getsendername(props.users,user).Name}</Text>
                    {typingUsers.includes(props.chatid)?<Text textAlign={'left'} height={'20px'} overflow={'hidden'} gap={'2px'} fontSize={'12px'} display={'flex'} color={'green'} >Typing ...</Text>:
                        <Text textAlign={'left'} height={'20px'} color={`${ selectedchat && (selectedchat._id==props.chatid)?'white':'black'}`}   overflow={'hidden'} gap={'2px'} fontSize={'12px'} display={'flex'}><span><b>{props.currchat.latestMessage?((props.currchat.latestMessage.sender.Name===user.Name)?"You":props.currchat.latestMessage.sender.Name):""}</b></span><span>{props.currchat.latestMessage?props.currchat.latestMessage.content:"Hey I am using Chatzen"}</span> </Text>}
                    
                </Box>


           </Container>



        </div>
      
    </div>
  )
}

export default UserChat
