import Context from '../Context/Context'
import React, { useContext } from 'react'
export const getsendername=(users,user)=>{
    
        
    let user1=users[0];
    let user2=users[1];
    if(user1._id===user._id){
        return user2;
    }
    return user1
}
export const getsenderemail=(users,user)=>{
    
    let user1=users[0];
    let user2=users[1];
    if(user1._id===user._id){
        return user2.email;
    }
    return user1.email
}
