import React, { useContext } from 'react'
import { useDisclosure } from '@chakra-ui/hooks';
import { Box, Button,Text } from '@chakra-ui/react';
import { Avatar, AvatarBadge, AvatarGroup } from '@chakra-ui/react'
import { Wrap, WrapItem } from "@chakra-ui/react";
import { ViewIcon } from '@chakra-ui/icons'
import {getsendername,getsenderemail} from '../Logic/Sender.js'

import {
    Modal,
    ModalOverlay,
    ModalContent,
    ModalHeader,
    ModalFooter,
    ModalBody,
    ModalCloseButton,
} from '@chakra-ui/react'
import Context from '../Context/Context.js';

const UserModal = (props) => {
    const { isOpen, onOpen, onClose } = useDisclosure()
    const {user}=useContext(Context)
    // console.log(user)
    return (

        <div style={{"borderRadius":'50px'}} >
            <div style={{ "color": "black" }} onClick={onOpen}>{props.userinfo?<ViewIcon boxSize={6} w={8} h={8} mr={'5px'} color="black"/>:'Your Info'}</div>

            <Modal isOpen={isOpen} onClose={onClose}>
                <ModalOverlay />
                <ModalContent>
                    <ModalHeader textAlign={'center'}>{props.userinfo?"UserInfo":'YourInfo'}</ModalHeader>
                    <ModalCloseButton />
                    <WrapItem margin={'auto'}>
                        <Avatar size='2xl' backgroundColor={'blue'} name='Segun Adebayo' src={props.userinfo?( props.userinfo.users &&user && `https://robohash.org/${getsendername(props.userinfo.users,user).Name}.png`):`https://robohash.org/${user.Name}.png`} />
                    </WrapItem>


                    <Box  width={'70%'} margin={'auto'} mt={'5px'} padding={'10px'}>
                    <Text color={'white'} width={'100%'} borderRadius={'10px'} backgroundColor={'gray'} padding={'5px'} mb={'5px'}>Name : {props.userinfo?( props.userinfo.users &&user && getsendername(props.userinfo.users,user).Name):user.Name} </Text>
                    <Text color={'white'} width={'100%'} fontSize={'1.3rem'} borderRadius={'10px'} backgroundColor={'gray'}padding={'5px'} >Mail :  {props.userinfo? props.userinfo.users &&user &&  getsenderemail(props.userinfo.users,user):user.email}  </Text>
                    </Box>

                    <ModalFooter>
                        <Button colorScheme='blue' mr={3} onClick={onClose}>
                            Close
                        </Button>
                    </ModalFooter>
                </ModalContent>
            </Modal>

        </div>
    )
}

export default UserModal
