import React ,{ useState ,useContext, useEffect} from "react";
import Context from "./Context.js";


const Userdata=(props)=>{

    const [user,setuser]=useState({});
    const [type,istype]=useState(false)
    const [typingUsers,setTypingUsers]=useState([])
    
    const [Alluser,setAlluser]=useState([]);
    const [allchats,setallchats]=useState([]);
    const [selectedchat,setselectedchat]=useState();
    const [currmess,setcurrmess]=useState([]);
    const [notification,setnotification]=useState([]);
    useEffect(()=>{
        setuser(JSON.parse(window.localStorage.getItem("user")))
    },[])

    return (
        <Context.Provider value={{ user,setuser,Alluser,setAlluser,allchats,setallchats,selectedchat,setselectedchat,currmess,setcurrmess,notification,setnotification,type,istype,typingUsers,setTypingUsers}}>
                {props.children}
            </Context.Provider>
      )
}

export default Userdata