import React, { useState, useContext } from 'react'
import Context from '../Context/Context';
import UserModel from '../SideDrawer/UserModal.js'
import { ArrowBackIcon } from '@chakra-ui/icons'
import { useMediaQuery } from "@chakra-ui/react";
import { Box, Text, Button, Container, Input, InputGroup, InputRightElement } from '@chakra-ui/react';
import { getsendername } from '../Logic/Sender';
import GroupChatModal from '../GroupChat/GroupChatModal';
import Usermessages from '../Messages/Usermessages.js'

const Chats = ({ refresh, setrefresh }) => {
  const { selectedchat, setselectedchat, user } = useContext(Context)

  

  const [isSmallerThan450] = useMediaQuery("(max-width: 750px)");

  return (
    <div style={{ 'color': 'black', 'height': '100%' }}  >
      <Box height={'7%'} alignContent={'center'} display={'flex'} justifyContent={'space-between'} textAlign={'right'}>
        <Box  display={isSmallerThan450 ? 'flex' : 'none'}  alignItems={'center'} ><i className="fa-solid fa-arrow-left fa-2xl " onClick={() => { setselectedchat() }}></i></Box>
        {(selectedchat) ?<Box m={'5px'} >  <Text fontSize={'1.5rem'}   textAlign={'center'} backgroundColor={'#fba988'}  color={'white'}  borderRadius={'5px'}>{(selectedchat.isGroupChat === true) ? selectedchat.chatName : getsendername(selectedchat.users, user).Name}</Text></Box> : ""}
        <Box p={'5px'}> 
          {selectedchat ? (selectedchat.isGroupChat === true) ? <GroupChatModal refresh={refresh} setrefresh={setrefresh} /> : <UserModel userinfo={selectedchat} /> : ""}
        </Box>
      </Box>


      {/* Messages Section */}
      
        {/* hiii */}
      <Box>
      {/* <Usermessages/> */}
      <Usermessages refresh={refresh} setrefresh={setrefresh}/>
      

      </Box>

    </div>
  )
}

export default Chats
