import React, { useState,useContext } from "react";
import {  Button,Text } from '@chakra-ui/react';
import { ChevronDownIcon } from "@chakra-ui/icons";
import Context from "../Context/Context";

import NotificationBadge, { Effect } from 'react-notification-badge';


import {
  Menu,
  MenuButton,
  MenuList,
  MenuItem,
  MenuItemOption,
  MenuGroup,
  MenuOptionGroup,
  MenuDivider,
} from "@chakra-ui/react";
import UserModal from "./UserModal";
import { useNavigate } from "react-router-dom";
import { getsendername } from "../Logic/Sender";





const User = () => {
  const {notification,setnotification,user,setselectedchat}=useContext(Context)
  const history=useNavigate();
  
  return (
    <div>
      <Menu>
        <MenuButton as={Button} m={'5px'}   >
          <NotificationBadge count={notification.length} effect={Effect.SCALE}
          />
            
        <i className="fa-regular fa-bell fa-2xl" style={{"color": "#e70d2e"}}></i>
        </MenuButton>
        <MenuList>       
         {/* { console.log("Message recieved" ,notification)   } */}
         
        {notification.length===0 && "No New Messages"}
        {notification && 
          notification.map((notify)=>{
            return<MenuItem
                  key={notify._id}
                  onClick={()=>{
                    setselectedchat(notify.chat)
                    setnotification(notification.filter((n)=>{return n!==notify}))
                  }}
                >
                  {notify.chat.isGroupChat
                    ? `New Message in ${notify.chat.chatName}`
                    : `New Message from ${getsendername( notify.chat.users,user).Name}`}
                </MenuItem>
          })

        }
        </MenuList>
        </Menu>
        <Menu>
        <MenuButton as={Button} colorScheme='teal'  _hover={{ bg: "blue" }} rightIcon={<ChevronDownIcon />}>
          <Text display={{ base: "none", md: "block" }} color={'white'}>User</Text>
          <Text display={{ base: "block", md: "none" }}><i className="fa-solid fa-user"></i></Text>
        </MenuButton>
        <MenuList>
          <MenuItem  ><UserModal /></MenuItem>
          <MenuItem color={'red'} onClick={()=>{window.localStorage.clear();history("/")}}>Log Out</MenuItem>
        </MenuList>
      </Menu>
    </div>
  );
};

export default User;
