import { Box, Heading, FormControl, FormLabel, Input, Button, Toast } from '@chakra-ui/react';
import { useState } from 'react';
import { useNavigate } from "react-router-dom";
import { useToast } from '@chakra-ui/react'
import './Login.css'

function Signin() {
  // all usestate
  const [name, setname] = useState("")
  const [email, setemail] = useState("")
  const [password, setpassword] = useState('')



  const toast = useToast()
  // history
  // let history=useNavigate();

  // Handle submit
  const Handlesubmit = async (e) => {
    e.preventDefault()
    document.querySelector(".Overlay").style.zIndex = "1001";
    const response = await fetch('https://mern-chat-app-steel.vercel.app/handleuser/', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ Name: name, email: email, password: password })
    });
    document.querySelector(".Overlay").style.zIndex = "-1";
    const json = await response.json()
    if (json.sucess === true) {
      toast({
        title: 'Account created.',
        description: "We've created your account for you.",
        status: 'success',
        duration: 9000,
        isClosable: true,
      })
    }
    else {
      toast({
        title: 'Account Already Exists',
        description: "Email already registered.",
        status: 'success',
        duration: 9000,
        isClosable: true,
      })
    }

  }
  return (
    <Box
      display="flex"
      justifyContent="center"
      alignItems="center"
      bgGradient="linear(to right, #f2994a, #f2c94c)"
    >
      <Box
        p={8}
        maxW="400px"
        bg="white"
        rounded="lg"
        boxShadow="lg"
      >
        <Heading mb={2} textAlign="center" color="gray.700">Sign Up</Heading>
        <FormControl isRequired id="name" >
          <FormLabel color="gray.700" mb={1}>Name</FormLabel>
          <Input color="gray.700" type="text" placeholder="Enter your name" value={name} onChange={(e) => { setname(e.target.value) }} required={true} />
        </FormControl>
        <FormControl isRequired id="email" >
          <FormLabel color="gray.700" mb={1}>Email address</FormLabel>
          <Input color="gray.700" type="email" placeholder="Enter your email" value={email} onChange={(e) => { setemail(e.target.value) }} required={true} />
        </FormControl>
        <FormControl isRequired id="password" >
          <FormLabel color="gray.700" mb={1}>Password</FormLabel>
          <Input color="gray.700" type="password" placeholder="Enter your password" value={password} onChange={(e) => { setpassword(e.target.value) }} required={true} />
        </FormControl>
        <FormControl isRequired id="confirmPassword" >
          <FormLabel color="gray.700" mb={1}>Confirm Password</FormLabel>
          <Input color="gray.700" type="password" placeholder="Confirm your password" required={true} />
        </FormControl>
        <Button mt={2} colorScheme="green" size="lg" width="full" onClick={Handlesubmit}>
          Sign Up
        </Button>
      </Box>
      <div className="Overlay" ></div>
    </Box>
  );
}

export default Signin;
